import { useCallback } from 'react'
import useOllama from './useOllama'
import { Agent, Hub } from '../stores/chatStore'
import { toast } from 'react-hot-toast'

interface ChatOllamaOptions {
  agent?: Agent | null
  hub?: Hub | null
  onChunk?: (chunk: string) => void
}

const useChatOllama = () => {
  const { generate, generateStream, generating } = useOllama()

  const sendMessage = useCallback(async (
    message: string,
    options: ChatOllamaOptions = {}
  ): Promise<string> => {
    const { agent, hub, onChunk } = options

    if (!message.trim()) {
      toast.error('Mensagem no pode estar vazia')
      return ''
    }

    try {
      // Usar configurao do agente se disponvel
      const model = agent?.configuration?.model || 'mistral:latest'
      const temperature = agent?.configuration?.temperature || 0.7
      const maxTokens = agent?.configuration?.maxTokens || 2048
      
      // Construir system prompt baseado no agente e hub
      let systemPrompt = ''
      if (agent && hub) {
        systemPrompt = `${agent.systemPrompt}\n\nVoc est atuando como ${agent.name} no hub ${hub.name}. ${hub.description}`
      } else if (agent) {
        systemPrompt = agent.systemPrompt
      } else {
        systemPrompt = 'Voc  um assistente til e prestativo.'
      }

      // Se h callback para chunks, usar streaming
      if (onChunk) {
        return await generateStream(message, {
          model,
          system: systemPrompt,
          temperature,
          max_tokens: maxTokens
        }, onChunk)
      } else {
        // Usar gerao normal
        const response = await generate(message, {
          model,
          system: systemPrompt,
          temperature,
          max_tokens: maxTokens
        })
        
        return response?.response || 'Desculpe, no consegui gerar uma resposta.'
      }
    } catch (error) {
      console.error('Erro ao enviar mensagem para Ollama:', error)
      toast.error('Erro ao gerar resposta com Ollama')
      return 'Desculpe, ocorreu um erro ao processar sua mensagem.'
    }
  }, [generate, generateStream])

  const isGenerating = generating

  return {
    sendMessage,
    isGenerating
  }
}

export default useChatOllama